import React from "react";
import ReactDOM from "react-dom";
import helpers from '../helpers/helperFunctions';

class Skill extends React.Component {
  constructor(props){
    super(props);
    this.canvasRef = React.createRef();
    this.skillRef = React.createRef();
    this.particleRadius = 3;
    this.skillParticles =[];
    this.rating = this.props.selfRating/10*2;
    this.animate = this.animate.bind(this);
    this.inToView = this.inToView.bind(this);
    this.particleSpeed = 0.05;
    this.timeout = 10;
    this.timeoutCounter = 0;
    this.timeoutLimit = this.timeout * skillColors.length;
    this.skillParticleCounter = 0;
    this.textTimeout = this.timeout * this.rating;
    this.animateDone = false;
    this.stopAnimation = false;
    this.state = {
      canvasWidth: 300,
      canvasHeight: 200,
      transition: ""
    }
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.inToView);
    this.stopAnimation = true;
  }

  inToView(ev) {
    if(this.animateDone) return;
    let elTop = this.skillRef.current.getBoundingClientRect().top;
    if(elTop < (window.innerHeight/2))
    {
      this.setState({transition: "translate"});
      this.animationStart = Date.now();
      this.stopAnimation = false;
      this.animate();
      this.animateDone = true;
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.inToView);
    this.sizeCanvas();
    this.createSkillParticles();
    this.text = new renderText(this.props.selfRating, this.c, this.state.canvasWidth, this.state.canvasHeight, this.pixelRatio, 60);
  }

  sizeCanvas()
  {
    this.pixelRatio = 2;
    this.setState({canvasWidth: this.state.canvasWidth * this.pixelRatio, canvasHeight: this.state.canvasHeight * this.pixelRatio});
    this.c = this.canvasRef.current.getContext('2d');
  }

  animate()
  {
    if(this.stopAnimation || Date.now()-this.animationStart>10000){ return; }//Stops animation loop after 5 second as it will be complete and we dont want overhead fo recursive function doing nothing
    requestAnimationFrame(this.animate);
    this.c.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight);
    if(this.timeoutCounter == this.timeout)
    {
      this.skillParticleCounter++;
      if(this.skillParticleCounter == (skillColors.length/2)+1) // For some reason there is an extra dot that assumes the same position as another one, cant figure how to get rid of it so for now it can animate at the same timeout as the dot it is covering
      {
        this.skillParticleCounter++;
      }
      this.timeoutCounter = 0;
    }
    for(let sp=0;sp<this.skillParticleCounter;sp++)
    {
      this.skillParticles[sp].update();
    }
    if(this.skillParticleCounter<this.rating)
    {
      this.timeoutCounter++;
    }
    if(this.skillParticleCounter>1)
    {
      this.text.update();
    }
  }

  createSkillParticles() {
    let x = this.state.canvasWidth / 2,
        y = this.state.canvasHeight,
        particles = this.rating,
        fourthQuartileDegree = 270, //start degrees at the left side of the semi circle
        firstQuartileDegree = 90, //end degrees at the right side of the semi circle
        degreeInterval = 180 / (skillColors.length), // interval of particle around semi circle minus the 2 at the right and left bounds
        tickerDegrees = fourthQuartileDegree, //Start ticker at 270 degrees;
        radians,
        moveToX,
        moveToY;

    for(let p=0;p<particles;p++)
    {
      tickerDegrees = tickerDegrees + degreeInterval;
      let center = (this.state.canvasWidth)/2 - (this.particleRadius*2);
      radians = tickerDegrees * (Math.PI/180);
      moveToX = (center + (center * Math.sin(radians))) + this.particleRadius;
      moveToY = (this.state.canvasHeight - center * Math.cos(radians)) + this.particleRadius;
      if(p == (skillColors.length/2))// when reached the top of the circle set ticker to 0
      {
        tickerDegrees = 0;
      }      
      this.skillParticles.push(new skillParticle(x, y, moveToX, moveToY, this.particleRadius, skillColors[p], this.particleSpeed, this.c, this.pixelRatio));
    }
  }

  render() {
    return (
      <div className={"skill " + this.state.transition} ref={this.skillRef}>
        <canvas ref={this.canvasRef} width={this.state.canvasWidth} height={this.state.canvasHeight} style={{height: this.state.canvasHeight/this.pixelRatio + 'px', width: this.state.canvasWidth/this.pixelRatio + 'px'}}/>
        <h2 className="t-center">
          {this.props.skill}
        </h2>
        <p>{this.props.desc}</p>
      </div>
    )
  }
}

function renderText(numberValue, c, width, height, timeout)
{
  this.timeoutCounter = 0;
  this.number = 0;

  this.update = function() {
    if(this.number < numberValue && this.timeoutCounter == timeout)
    {
      this.number++;
      this.timeoutCounter = 0;
    }
    this.timeoutCounter++;
    this.draw();
  }

  this.draw = function() {
    c.font = '34px monospace';
    c.textAlign = 'center';
    c.fillText(`${this.number}%`, width/2, height-40);
  }
}

function skillParticle(x, y, moveToX, moveToY, radius, color, speed, c, pixelRatio)
{
  Object.assign(this, {x, y, moveToX, moveToY, radius, color, speed, c});

  this.update = function() {
 
    this.x += (this.moveToX-this.x) * this.speed;
    this.y += (this.moveToY-this.y) * this.speed;
    this.draw();
  }

  this.draw = function() {
    c.setTransform(pixelRatio,0,0,pixelRatio,0,0);
    c.beginPath();
    c.arc(this.x, this.y, this.radius, 0, Math.PI *2, false);
    c.fillStyle = color;
    c.fill();
    c.closePath();
  }
}



let skillColors = [
  "#ff4100",
  "#f44242",
  "#f44641",
  "#f44d41",
  "#f45541",
  "#f45e41",
  "#f46741",
  "#f47f41",
  "#f48541",
  "#f4a641",
  "#f4dc41",
  "#f4be41",
  "#f4c741",  
  "#f1f441",
  "#f4ee41",
  "#dff441",
  "#b5f441",
  "#91f441",
  "#6af441",
  "#55f441"
];

export default Skill;