import React from "react";
import ReactDOM from "react-dom";
import Keyboards from '../data/keyboards';
import helpers from '../helpers/helperFunctions';

class AnimatedKeyboard{
  constructor(canvasID, width, height)
  {
    this.canvas = document.getElementById(canvasID);
    if(!this.canvas){return;}
    this.firstblood = true;
    this.c = this.canvas.getContext('2d');
    this.stopAnimation = false;
    this.animate = this.animate.bind(this);
    this.width = width;
    this.height = height;    
    helpers.scaleCanvas(this.canvas, this.c, this.width, this.height);
    this.particleSpeedMin = 0.001;
    this.particleSpeedMax = 0.002;
    this.expandBy = 6;
    this.keyIsDown = false;
    this.keyboardParticles =[];
    this.keyboardKeys = [];
    this.createResponsive();
    this.xStart = (this.width - this.keyboardWidth) / 2;
    this.yStart = (this.height - this.keyboardHeight) / 2;
    this.startXs = [0, this.width];
    this.startYs = [0, this.height];
    this.createKeyboardParticles();
    this.bindEventListeners();
    this.canvas.focus();
    this.animate();    
  }

  bindEventListeners()
  {
    this.canvas.addEventListener('mousedown', (ev)=>{this.keyDown(ev)});
    this.canvas.addEventListener('mouseup', (ev)=>{this.keyNormal(ev)});
    this.canvas.addEventListener('touchstart', (ev)=>{this.touchDown(ev)});
    this.canvas.addEventListener('touchend', (ev)=>{this.keyNormal(ev)});
    this.canvas.addEventListener('keydown', (ev)=>{this.keyboardKeyDown(ev)});
    this.canvas.addEventListener('keyup', (ev)=>{this.keyboardKeyDown(ev)});
    this.canvas.addEventListener('mousemove', (ev)=>{this.keyUp(ev)});
  }

  createResponsive()
  {
    let minWidthForDesktop = 1140;
    let minWidthForTablet = 640;
    let minWidthForMobile = 390;
    if(this.width > minWidthForDesktop)
    {
      this.keyboard = Keyboards.MAC;
      this.keyboardWidth = minWidthForDesktop;
      this.particleSpacing = 10;
      this.keySpacing = 30;
      this.rowSpacing = 80;
      this.keyboardHeight = 370;
      this.particleFloatingRadius = 1;  
    }
    else if (this.width > minWidthForTablet && this.width < minWidthForDesktop)
    {
      this.keyboard = Keyboards.MACMobile;
      this.keyboardWidth = minWidthForTablet;
      this.particleSpacing = 10;
      this.keySpacing = 26;
      this.rowSpacing = 90;
      this.keyboardHeight = 425;
      this.particleFloatingRadius = 1;
    }
    else
    {
      this.keyboard = Keyboards.MACMobile;
      this.keyboardWidth = minWidthForMobile;
      this.particleSpacing = 6;
      this.keySpacing = 16;
      this.rowSpacing = 70;
      this.keyboardHeight = 320;  
      this.particleFloatingRadius = 0;    
    }
  }

  createKeyNames() {
    for(let key of this.keyboardParticles)
    {
      let x = (key.keyBox.endX - key.keyBox.startX)/ 2 + key.keyBox.startX;
      let y = (key.keyBox.endY - key.keyBox.startY)/2 + key.keyBox.startY;
      this.keyboardKeys.push(new Key(this.c, key.key, x, y));
    }
  }

  keyInRange(direction, x, y)
  {
    x = x;
    y = y;
    for(let kcp of this.keyboardParticles)
    {
      for(let part of kcp.particles)
      { 
        if(x > kcp.keyBox.startX && x < kcp.keyBox.endX && y > kcp.keyBox.startY && y < kcp.keyBox.endY && direction != "normal")
        {
          part.expand = true;
          this.growShrink(direction, part, kcp);
        }
        else
        {
          part.expand = false;
        }
      }
    }   
  }

  growShrink(direction, part, kcp)
  { 
    if(part.moveToX >= (kcp.keyBox.startX-this.particleFloatingRadius) && part.moveToX <= (kcp.keyBox.startX +this.particleFloatingRadius))
    {
      if(direction=="inwards")
      {
        part.expandXPos = part.moveToX + this.expandBy;
        delete part.expandXNeg;
      }
      else
      {
        part.expandXNeg = part.moveToX - this.expandBy;
        delete part.expandXPos;
      }       
    }
    if(part.moveToX >= (kcp.keyBox.endX-this.particleFloatingRadius) && part.moveToX <= (kcp.keyBox.endX +this.particleFloatingRadius))
    {
      if(direction=="inwards")
      {
        part.expandXNeg = part.moveToX - this.expandBy;
        delete part.expandXPos;
      }
      else
      {
        part.expandXPos = part.moveToX + this.expandBy;
        delete part.expandXNeg;
      }
    }
    if(part.moveToY >= (kcp.keyBox.startY-this.particleFloatingRadius) && part.moveToY <= (kcp.keyBox.startY +this.particleFloatingRadius))
    {
      if(direction=="inwards")
      {
        part.expandYPos = part.moveToY + this.expandBy;
        delete part.expandYNeg;
      }
      else
      {
        part.expandYNeg = part.moveToY - this.expandBy;
        delete part.expandYPos;
      }
    }
    if(part.moveToY >= (kcp.keyBox.endY-this.particleFloatingRadius) && part.moveToY <= (kcp.keyBox.endY +this.particleFloatingRadius))
    {
      if(direction=="inwards")
      {
        part.expandYNeg = part.moveToY - this.expandBy;
        delete part.expandYPos;
      }
      else
      {
        part.expandYPos = part.moveToY + this.expandBy;
        delete part.expandYNeg;
      }
    }
  }

  keyboardKeyDown(ev)
  {
    ev.preventDefault();
    for(let kcp of this.keyboardParticles)
    {
      for(let part of kcp.particles)
      { 
        if(kcp.keyCode == ev.keyCode && ev.type == 'keydown')
        {
          part.expand = true;
          this.growShrink("inwards", part, kcp);
        }
        else
        {
          part.expand = false;
        }
      }
    }
    return false;
  }

  keyNormal(ev)
  {
    this.keyIsDown = false;
    this.keyInRange("normal", ev.clientX, ev.clientY-this.canvas.getBoundingClientRect().top);
  }

  touchDown(ev)
  {
    this.keyIsDown = true;
    this.keyInRange("inwards", ev.touches[0].clientX, ev.touches[0].clientY-this.canvas.getBoundingClientRect().top);
  }

  keyDown(ev)
  {
    this.keyIsDown = true;
    this.keyInRange("inwards", ev.clientX, ev.clientY-this.canvas.getBoundingClientRect().top);
  }

  keyUp(ev) {
    if(!this.keyIsDown)
    {
      this.keyInRange("outwards", ev.clientX, ev.clientY-this.canvas.getBoundingClientRect().top);
    }
  }

  createKeyboardParticles()
  {
    let x = this.xStart,
        y = this.yStart,
        xIndent = 0,
        yIndent = 0,
        xNegIndent = 0,
        yNegIndent = 0,
        xNegIndent2 = 0,
        xBounds,
        yBounds,
        xMax,
        startX,
        startY,
        bezierControlPoints;
    for (let row of this.keyboard.keyboardRows)
    {
      for(let key of row.keys)
      {
        let particles = [];
        let keyBox = {};
        let iterator = key.particles ? key.particles : this.keyboard.particles;
        if(key.extraSpace)
        {
          let extraSpace = key.extraSpaceDivider ? this.keySpacing / key.extraSpaceDivider : this.keySpacing;
          x = x + extraSpace;
        }        
        for(let i=0;i<iterator;i++)
        {
          if(key.type == 'special')
          {
            xBounds = key.x;
            yBounds = key.y;
          }
          else
          {
            xBounds = key.width ? key.width : this.keyboard.width;
            yBounds = this.keyboard.height;            
          }

          if(xIndent == xBounds)
          { 
            if(yIndent == yBounds)
            {
              xBounds = key.type == 'special' ? key.x2 : xBounds;
              if(xNegIndent == xBounds)
              {
                if(key.type == 'special')
                {
                  yBounds = key.y2;
                  if(yNegIndent == yBounds)
                  {
                    xBounds = key.x3;
                    if(xNegIndent2 == xBounds)
                    {
                      y = y - this.particleSpacing;
                    }
                    else
                    {
                      x = x - this.particleSpacing;
                      xNegIndent2++;
                    }
                  }
                  else
                  {
                    yNegIndent++;
                    y = y - this.particleSpacing;
                  }
                }
                else
                {
                  y = y - this.particleSpacing;
                }
              }
              else
              {
                x = x - this.particleSpacing;
                xNegIndent++;
              }
              keyBox.startY = y;
            }
            else
            {
              y = y + this.particleSpacing;
              keyBox.endY = y;
              yIndent++;
            }
            keyBox.startX = x;
          }
          else
          {
            x = x + this.particleSpacing;
            keyBox.endX = x;
            xIndent++;
          }
          startX = this.startXs[Math.floor(Math.random()*this.startXs.length)];
          startY = this.startYs[Math.floor(Math.random()*this.startYs.length)];
          
          if((startX == 0 && startY == 0) || (startX == this.width && startY == this.height))
          {
            startX = helpers.randomInt(0, this.width);
          }
          else if((startX == this.width && startY == 0) || (startY == this.height && startX == 0))
          {
            startY = helpers.randomInt(0, this.height);
          }

          bezierControlPoints = {
            x1: helpers.randomInt(0, this.width),
            y1: helpers.randomInt(0, this.height),
            x2: helpers.randomInt(0, this.width),
            y2: helpers.randomInt(0, this.height)
          }
          let radius = helpers.randomInt(120, 250) / 100;
          particles.push(new KeyBoardParticle(startX, startY, x, y, radius, (Math.random() * (this.particleSpeedMax - this.particleSpeedMin) + this.particleSpeedMin), bezierControlPoints, this.c, this.particleFloatingRadius));
        }
        
        this.keyboardParticles.push({key:key.name, keyCode:key.keyCode, keyBox: keyBox, particles});
        x = x + (xBounds * this.particleSpacing) + this.keySpacing;
        xIndent = 0;
        yIndent = 0;
        xNegIndent = 0;
        yNegIndent = 0;
      }
      y = y + this.rowSpacing;
      x = this.xStart;
    }
  }

  animate()
  {
    if(this.stopAnimation){ return; }
    requestAnimationFrame(this.animate);
    this.c.clearRect(0, 0, this.width, this.height);
    for(let key of this.keyboardKeys)
    {
      key.update();
    }
    for(let keys of this.keyboardParticles)
    {
      for(let kp of keys.particles)
      {
        kp.update();  
      }
    }
  }
}

function Key(c, name, x, y)
{
  Object.assign(this, {c, name, x, y});
  this.opacity = 0;
  this.update = function() {
    if(this.opacity < 1)
    {
      this.opacity += 0.005;
    }
    this.draw();
  }
  this.draw = function () {
    c.globalAlpha = this.opacity;
    c.font = '12px monospace';
    c.textAlign = 'center';
    c.textBaseline = 'middle';
    c.fillStyle = "white";
    c.fillText(this.name, this.x, this.y);
    c.globalAlpha = 1;
  }
}

function KeyBoardParticle(x, y, moveToX, moveToY, radius, drag, bezierControlPoints, c, particleFloatingRadius) {
  Object.assign(this, {x, y, moveToX, moveToY, radius, drag, bezierControlPoints, c, particleFloatingRadius});
  this.radians = 0;
  this.velocity = 0.05;
  this.groVelocity = 0.8;
  this.t = 0;
  this.normalRadius = 1.5;
  this.update = function() {

    this.p0 = {x:x, y:y};
    this.p1 = {x:bezierControlPoints.x1, y:bezierControlPoints.y1};
    this.p2 = {x:bezierControlPoints.x2, y:bezierControlPoints.y2};
    this.p3 = {x:moveToX, y:moveToY};
    this.cx = 3 * (this.p1.x - this.p0.x)
    this.bx = 3 * (this.p2.x - this.p1.x) - this.cx;
    this.ax = this.p3.x - this.p0.x - this.cx - this.bx;
    this.cy = 3 * (this.p1.y - this.p0.y);
    this.by = 3 * (this.p2.y - this.p1.y) - this.cy;
    this.ay = this.p3.y - this.p0.y - this.cy - this.by;
    this.t += this.drag;
  
    if (this.t >= 1) {
      this.t = 1;
      this.radius = this.normalRadius;
      if(!this.expand)
      {
        this.radians += this.velocity;
        this.y = this.moveToY + Math.cos(this.radians) * particleFloatingRadius;  
        this.x = this.moveToX - Math.sin(this.radians) * particleFloatingRadius;          
      }
      else
      {
        if(this.expandXNeg && this.x > this.expandXNeg)
        {
          this.x -= this.groVelocity ;
        }
        if(this.expandXPos && this.x < this.expandXPos)
        {
          this.x += this.groVelocity;
        }
        if(this.expandYPos && this.y < this.expandYPos)
        {
          this.y += this.groVelocity;
        } 
        if(this.expandYNeg && this.y > this.expandYNeg)
        {
          this.y -= this.groVelocity;
        }      
      }
    }
    else
    {
      this.x = this.ax*(this.t*this.t*this.t) + this.bx*(this.t*this.t) + this.cx*this.t + this.p0.x;
      this.y = this.ay*(this.t*this.t*this.t) + this.by*(this.t*this.t) + this.cy*this.t + this.p0.y;
    }
    this.draw();
  }

  this.draw = function() {
    c.beginPath();
    c.arc(this.x, this.y, this.radius, 0, Math.PI *2, false);
    c.fillStyle = "white";
    c.fill();
    c.closePath();
  }
}

class AnimatedKeyboardReact extends React.Component {
  componentWillUnmount()
  {
    this.animation.stopAnimation = true;
  }

  componentDidMount()
  {
    this.animation = new AnimatedKeyboard('animated-keyboard', this.props.width, this.props.height);
  }

  render() {
    return (
      <canvas id="animated-keyboard" tabIndex='1'></canvas>
    )
  }  
}

export default AnimatedKeyboardReact;