import React from "react";
import ReactDOM from "react-dom";
import * as THREE from 'three';
import helpers from '../helpers/helperFunctions';
import routes from '../data/routes';
let EasingFunctions = helpers.getEasingFunctions();

class Universe extends React.Component {
  constructor(props)
  {
    super(props);
    this.helpers = false;
    this.mainObjectColor = new THREE.Color( 0xf47740 );
    this.mainObjectColor.convertSRGBToLinear();
    this.mainObjectNotMoving = true;
    this.animationDuration = 1500;
    this.rotationNorm = 0.005;
    this.movingSpinSpeed = 0.4;
    this.mouseMoveDrag = 0.02;
    this.mouseMoveBounds = 0.0001;
    this.yRotation = this.rotationNorm;
    this.zRotation = this.rotationNorm;
    this.mouse = new THREE.Vector2();
    this.prevMouse = new THREE.Vector2();
    this.cameraPositions = {
      center: {
        x: 0,
        y: 0,
        z: 50
      },
      bottomRight: {
        x: -11,
        y: 6,
        z: 20
      },
      topRight: {
        x: -11,
        y: -6,
        z: 20
      },
      topLeft: {
        x: 11,
        y: -6,
        z: 20
      },
      bottomLeft: {
        x: 11,
        y: 6,
        z: 20        
      }
    };
    this.lightPositions = {
      center: {
        x: 10,
        y: 10,
        z: 15
      },
      bottomRight: {
        x: -20,
        y: -20,
        z: 40
      },
      topRight: {
        x: -20,
        y: 20,
        z: 40
      },
      topLeft: {
        x: -20,
        y: 20,
        z: 40
      },
      bottomLeft: {
        x: 20,
        y: -20,
        z: 40        
      }
    };
    this.initialDirection = "bottomRight";
    for(let route of routes)
    {
      if(window.location.pathname != "/" && route.path == window.location.pathname)
      {
        this.initialDirection = route.position;
      }
    }
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.rotations = ['yRotation', 'zRotation'];
    window.addEventListener('resize', ()=>{this.onResize()}); 
  }

  render() {
    return (
      <div className="universe-container" ref={element => this.container = element}></div>
    );
  }

  componentDidMount()
  {
    this.elementHalf = new THREE.Vector2( window.innerWidth / 2, window.innerHeight / 2 );
    this.createScene();
    this.createCamera();
    this.createLights();
    this.createMainObject();
    this.createRenderer();
    this.animeLoop = ()=>{
      this.update();
      this.renderUniverse();      
    }
    this.renderer.setAnimationLoop(this.animeLoop);
  }

  haltAnimations()
  {
    this.renderer.setAnimationLoop(null);
  }

  resumeAnimations()
  {
    this.renderer.setAnimationLoop(this.animeLoop);
  }

  removeMouseMove()
  {
    document.body.removeEventListener('mousemove', this.handleMouseMove);
  }

  addMouseMove()
  {
    document.body.addEventListener('mousemove', this.handleMouseMove);
  }

  handleMouseMove(e)
  {
    this.mouse.x = ( event.clientX - this.elementHalf.x );
    this.mouse.y = ( event.clientY - this.elementHalf.y );    
  }

  easingFunction(timingfn, flag, duration, variable='start')
  {
    if(!this[variable])
    {
      this[variable] = performance.now();
    }
    let p = (performance.now() - this[variable]) / duration;
    let val  = EasingFunctions[timingfn](p);
    if (performance.now() - this[variable] > duration){
      delete this[variable];
      if(flag){this[flag] = true;}
      return 1;
    }
    else
    {
      return val.toFixed(8);      
    }
  }

  moveMainObject(moveTo)
  {
    moveTo == "center" ? this.addMouseMove() : this.removeMouseMove();
    this.start = false;
    this.start2 = false;
    this.start3 = false;
    this.mainObjectMoved = false;
    this.halfspin = false;
    this.cacheCurrentRotation = false;
    this.moveTo = moveTo;
    this.spinDirection = this.rotations[Math.floor(Math.random() * this.rotations.length)];
    this.currentShapeConditions = {
      x: this.camera.position.x,
      y: this.camera.position.y,
      z: this.camera.position.z,
      yRotation: this.yRotation,
      zRotation: this.zRotation
    }
    this.directionalLight.position.set(this.lightPositions[this.moveTo].x , this.lightPositions[this.moveTo].y, this.lightPositions[this.moveTo].z);
    this.mainObjectNotMoving = false; 
  }

  mainObjectMove()
  {
    let val = this.easingFunction('easeInOutQuad', 'mainObjectNotMoving', this.animationDuration);
    let mx = this.cameraPositions[this.moveTo].x;
    let my = this.cameraPositions[this.moveTo].y;
    let mz = this.cameraPositions[this.moveTo].z;
    if(this.currentShapeConditions.x != mx)
    {
      if(mx == 0)
      {
        this.camera.position.x = this.currentShapeConditions.x * (1-val);
      }
      else
      {
        this.camera.position.x = this.currentShapeConditions.x + (mx - this.currentShapeConditions.x) * val;
      }
    }
    if(this.currentShapeConditions.y != my)
    {
      if(my == 0)
      {
        this.camera.position.y = this.currentShapeConditions.y * (1-val);
      }
      else
      {
        this.camera.position.y = this.currentShapeConditions.y + (my - this.currentShapeConditions.y) * val;
      }
    }
    if(this.currentShapeConditions.z != mz)
    {
      if(mz == 0)
      {
        this.camera.position.z = this.currentShapeConditions.z * (1-val);
      }
      else
      {
        this.camera.position.z = this.currentShapeConditions.z + (mz - this.currentShapeConditions.z) * val;
      }
    }
    this.spin(val);    
  }

  spin(val2)
  {
    if(!this.halfspin)
    {
      let val = this.easingFunction('easeOutQuint', 'halfspin', this.animationDuration/2, 'start2');
      this[this.spinDirection] = this.currentShapeConditions[this.spinDirection] + (this.movingSpinSpeed - this.currentShapeConditions[this.spinDirection])*val;
    }
    else
    {
      let val = this.easingFunction('easeOutCubic', 'mainObjectMoved', this.animationDuration/2, 'start3')
      if(!this.cacheCurrentRotation)
      {
        this.currentShapeConditions[this.spinDirection] = this[this.spinDirection];
      }
      this[this.spinDirection] = this.currentShapeConditions[this.spinDirection] + (this.rotationNorm - this.currentShapeConditions[this.spinDirection])*val;
      this.cacheCurrentRotation = true;
    }
  }

  createScene()
  {
    this.scene = new THREE.Scene();  
  }

  createCamera()
  {
    const fieldOfView = 35;
    const aspectRatio = window.innerWidth / window.innerHeight;
    const nearViewingPlane = 0.1;
    const farViewingPlane = 100;
    this.camera = new THREE.PerspectiveCamera( fieldOfView, aspectRatio, nearViewingPlane, farViewingPlane );
    this.camera.position.set( this.cameraPositions[this.initialDirection].x , this.cameraPositions[this.initialDirection].y, this.cameraPositions[this.initialDirection].z );
    if(this.helpers)
    {
      var helper = new THREE.CameraHelper( this.camera );
      this.scene.add( helper );      
    }
  }

  createLights()
  {
    this.directionalLight = new THREE.DirectionalLight( 0x505050, 15 );
    this.directionalLight.position.set(this.lightPositions[this.initialDirection].x , this.lightPositions[this.initialDirection].y, this.lightPositions[this.initialDirection].z);
    this.scene.add( this.directionalLight );
    const light2 = new THREE.AmbientLight( 0x505050, 7 );
    this.scene.add( light2 );  
    if(this.helpers)
    {
      var helper = new THREE.DirectionalLightHelper( this.directionalLight, 5 );
      this.scene.add(helper);
    }
  }

  createMainObject()
  {
    const mainObject = new THREE.TorusKnotBufferGeometry( 2, 3.6, 64, 10, 8, 17 );
    const mainObjectMaterial = new THREE.MeshStandardMaterial( { color: this.mainObjectColor, flatShading: true } );
    this.mainObjectMesh = new THREE.Mesh( mainObject, mainObjectMaterial );
    this.scene.add( this.mainObjectMesh );
  }

  createRenderer()
  {
    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    this.renderer.setSize( window.innerWidth, window.innerHeight );
    this.renderer.setPixelRatio( window.devicePixelRatio );
    // this.renderer.gammaFactor = 2.2;
    // this.renderer.gammaOutput = true;
    this.renderer.physicallyCorrectLights = true;
    this.container.appendChild( this.renderer.domElement );
  }

  renderUniverse()
  {
    this.renderer.render( this.scene, this.camera );
  }

  update()
  {
    if(!this.mainObjectNotMoving)
    {
      this.mainObjectMove();
    }
    else
    {
      this.zRotation = this.rotationNorm;
      this.yRotation = this.rotationNorm;
    }
    if(this.moveTo == 'center')
    {
      this.prevMouse.x = ( 1 - this.mouse.x ) * this.mouseMoveBounds;
      this.prevMouse.y = ( 1 - this.mouse.y ) * this.mouseMoveBounds;
      this.camera.rotation.x += this.mouseMoveDrag * ( this.prevMouse.y - this.camera.rotation.x );
      this.camera.rotation.y += this.mouseMoveDrag * ( this.prevMouse.x - this.camera.rotation.y );
    }
    this.mainObjectMesh.rotation.z += this.zRotation;
    this.mainObjectMesh.rotation.y += this.yRotation;
  }

  onResize()
  {
    this.camera.aspect = this.container.clientWidth / this.container.clientHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize( this.container.clientWidth, this.container.clientHeight );     
  }
}

export default Universe;