require("@babel/polyfill");
require('../scss/index.scss');

import Universe from './components/universe';
import UniverseStars from './components/universeStars';
import Navigation from './components/navigation';
import PersonalDetails from './components/personalDetails';
import React from "react";
import ReactDOM from "react-dom";
import {BrowserRouter} from "react-router-dom";

let mouseClickImage = require('../images/mouse-left-button.svg')

class App extends React.Component {
  constructor(props)
  {
    super(props);
    this.mainObjectCentered = this.mainObjectCentered.bind(this);
    this.documentClickFunction = this.documentClickFunction.bind(this);
    this.welcomeMessageFinished = this.welcomeMessageFinished.bind(this);
    this.noAppAnimations = this.noAppAnimations.bind(this);
    this.appAnimations = this.appAnimations.bind(this);
    this.newAppOpen = this.newAppOpen.bind(this);
    this.newAppClose = this.newAppClose.bind(this);
    this.debug = true;
    this.state = {
      mainObjectCentered: false,
      showClickIcon: false,
      showPersonalDetails: false,
      mainAppSlideOut: false
    }
  }
  componentDidMount() {
    let ua = window.navigator.userAgent;
    let isIE = /MSIE|Trident/.test(ua);
    let isChrome = /Chrome/.test(ua);
    if(isIE){document.documentElement.classList.add('ie');}
    let alreadyRan = window.localStorage.getItem('watchedInitialAnime');
    if(alreadyRan)
    {
      this.universeStars.fastForwardParticles();
    }
    else
    {
      window.localStorage.setItem('watchedInitialAnime', true);
    }
    if(window.location.pathname == "/")
    {
      this.setState({showPersonalDetails: true});
    }
  }

  newAppOpen(slide=true)
  {
    slide && 
    this.setState({
      mainAppSlideOut: true
    });
    this.noAppAnimations();
  }

  newAppClose()
  {
    this.setState({
      mainAppSlideOut: false
    });
    this.appAnimations();
  }

  appAnimations()
  {
    this.universe.resumeAnimations();
    this.universeStars.resumeAnimations();
  }

  noAppAnimations()
  {
    this.universe.haltAnimations();
    this.universeStars.haltAnimations();
  }

  welcomeMessageFinished()
  {
    if(window.location.pathname == "/")
    {
      document.body.addEventListener('click', this.documentClickFunction);
      this.setState({showClickIcon: true});      
    }
  }

  documentClickFunction()
  {
    this.universe.moveMainObject('center')
    this.universeStars.explodeStars();
    this.mainObjectCentered();
    this.setState({showClickIcon: false, showPersonalDetails: false});
    document.body.removeEventListener('click', this.documentClickFunction); 
  }

  navigationOccured(direction)
  {
    this.universe.moveMainObject(direction);
    direction == "center" ? this.universeStars.zoom('in') : this.universeStars.zoom('out');
  }

  mainObjectCentered() {
    this.setState({
      mainObjectCentered: true
    });
  }

  render() {
    return (
      <BrowserRouter>
        <div className={"app-area " + (this.state.mainAppSlideOut ? "slide-out" : "")}>
          <Universe ref={element => this.universe = element} />
          {this.state.showClickIcon && <img src={mouseClickImage} className="img-fluid click-icon click-icon-show"/>}
          
          <UniverseStars welcomeMessageFinished={this.welcomeMessageFinished} ref={element => this.universeStars = element} />        
          <Navigation newAppOpen={this.newAppOpen} newAppClose={this.newAppClose} isMainObjectCentered={this.state.mainObjectCentered} navigationOccured={this.navigationOccured.bind(this)}/>
          {
            this.state.showPersonalDetails &&
            <PersonalDetails />
          }
        </div>
      </BrowserRouter>
    );
  }
}

ReactDOM.render(
  <App />,
  document.getElementById('app')
);