import React from "react";
import helpers from '../helpers/helperFunctions';

class BlackHole {
  constructor(canvasID, width, height, centreText)
  {
    this.canvas = document.getElementById(canvasID);
    if(!this.canvas){return;}
    this.c = this.canvas.getContext('2d');
    this.stopAnimation = false;
    this.animate = this.animate.bind(this);
    this.sizeCanvas(width, height);
    this.generateTextNode(centreText);

    //Wait for clientBounding Rect to return true vals so we can determine inner ring radius
    setTimeout(() => {
      let dimensions = document.querySelector('.black-hole-centre-text').getBoundingClientRect();
      let irr = dimensions.width > dimensions.height ? dimensions.width : dimensions.height;
      this.innerRingRadius = irr > this.maxInnerRingRadius ? this.maxInnerRingRadius : irr;
      this.generateConfiguration();
      this.generateParticles();
      this.animate();      
    }, 500);
  }

  sizeCanvas(width, height)
  {
    this.width = width;
    this.height = height;
    this.halfWidth = (width/2);
    this.halfHeight = (height/2);
    this.centerPoints = {
      x: this.halfWidth,
      y: this.halfHeight
    };
    helpers.scaleCanvas(this.canvas, this.c, this.width, this.height);    
  }

  generateTextNode(centreText)
  {
    let text = document.createElement("div");
    text.classList.add("black-hole-centre-text");
    text.innerHTML = centreText;
    let container = document.createElement("div");
    container.classList.add('black-hole-container');
    this.canvas.parentNode.insertBefore(container, this.canvas);
    container.appendChild(text);
    container.appendChild(this.canvas);    
  }

  generateConfiguration()
  {
    this.particles = [];
    this.numberOfParticles = 500;
    this.colors = [
      "#FF355E",
      "#FF6037",
      "#FF7A00",
      "#FF6EFF", 
      "#FF00CC",
      "#FF00CC",
      "#50BFE6",
      "#7FDBFF",
      "white"
    ];
    this.maxInnerRingRadius = 300;
    this.innerParticleCountPercent = 0.5;
    this.outerParticleCountPercent = 0.4;
    this.innerRingRadiusBoundsPercentage = 0.20;
    this.outerRingRadiusBoundsPercentage = 0.25;
    this.particleRadiusLowerLimit = 1;
    this.particleRadiusUpperLimit = 2.5;
    this.velocityBounds = {
      inner: {
        min: 0.002,
        max: 0.003
      },
      middle: {
        min: 0.002,
        max: 0.001
      },
      outer: {
        min: 0.0009,
        max: 0.0006   
      }
    };
    this.isLandscape = this.width > this.height;
    this.aspectRatio = this.isLandscape ? (this.width / this.height) : (this.height / this.width);
    this.elongationToChange = this.isLandscape ? "x" : "y";
    this.innerParticleCountLimit = this.numberOfParticles * this.innerParticleCountPercent;
    this.outerParticleCountLimit = this.innerParticleCountLimit + (this.numberOfParticles * this.outerParticleCountPercent);
    this.useWidthOrHeight = this.isLandscape ? this.halfHeight : this.halfWidth;
    this.innerRingRadiusLimit = (this.useWidthOrHeight * this.innerRingRadiusBoundsPercentage) + this.innerRingRadius;
    this.outerRingRadiusLimit = (this.useWidthOrHeight * this.outerRingRadiusBoundsPercentage) + this.innerRingRadiusLimit;
    this.maxRingRadiusLimit = this.useWidthOrHeight;
  }

  generateParticles()
  {
    let radius,
        color,
        rangle,
        rotationRadius,
        radians,
        velocity,
        randomElongationMultiple,
        respawnRadius,
        respawnElongation,
        elongation;
        
    for(let i=0;i<this.numberOfParticles;i++)
    {
      elongation = {
        x: 1,
        y: 1
      };
      respawnElongation = {
        x: 1,
        y: 1
      };
      radius = helpers.randomInt(this.particleRadiusLowerLimit, this.particleRadiusUpperLimit);   
      radians = helpers.randomInt(0, 360); // spawn on random point on circumference
      if(i<=this.innerParticleCountLimit)
      {
        rotationRadius = helpers.randomInt(this.innerRingRadius, this.innerRingRadiusLimit);
        velocity = helpers.randomFloat(this.velocityBounds.inner.min, this.velocityBounds.inner.max);
        randomElongationMultiple = helpers.randomFloat(0.1, 0.3);
        elongation[this.elongationToChange] = 1 + ((this.aspectRatio - 1) * randomElongationMultiple);
      }
      else if(i>this.innerParticleCountLimit && i <=this.outerParticleCountLimit)
      {
        rotationRadius = helpers.randomInt(this.innerRingRadiusLimit, this.outerRingRadiusLimit);
        velocity = helpers.randomFloat(this.velocityBounds.middle.min, this.velocityBounds.middle.max);
        randomElongationMultiple = helpers.randomFloat(0.3, 0.5);
        elongation[this.elongationToChange] = 1 + ((this.aspectRatio - 1) * randomElongationMultiple);
      }
      else
      {
        rotationRadius = helpers.randomInt(this.outerRingRadiusLimit, this.maxRingRadiusLimit);
        velocity = helpers.randomFloat(this.velocityBounds.outer.min, this.velocityBounds.outer.max);
        randomElongationMultiple = helpers.randomFloat(0.5, 0.7);
        
      }
      elongation[this.elongationToChange] = 1 + ((this.aspectRatio - 1) * randomElongationMultiple);
      respawnElongation[this.elongationToChange] = 1 + ((this.aspectRatio - 1) * helpers.randomFloat(0.1, 0.7));
      respawnRadius = helpers.randomInt(this.innerRingRadius, this.maxRingRadiusLimit);
      this.particles.push({
        x: 0,
        y: 0,
        radians,
        radius,
        color: "white",
        respawnRadius,
        respawnElongation,
        rotationRadius,
        velocity,
        elongation,
        debug: (i==0)
      });
    }
  }

  updateParticles()
  {
    let distance, counter=0;
    for(let p of this.particles)
    {
      if(p.rotationRadius > this.innerRingRadius)
      {
        p.rotationRadius -= 0.1;
      }
      else
      {
        p.rotationRadius = p.respawnRadius;
        p.elongation = p.respawnElongation;
      }
      p.radians += p.velocity;
      p.y = this.centerPoints.y + Math.sin(p.radians) * p.rotationRadius * p.elongation.y; 
      p.x = this.centerPoints.x + Math.cos(p.radians) * p.rotationRadius * p.elongation.x;
      distance = helpers.getDistance(p.x, p.y, this.width/2, this.height/2);
      if(distance < this.innerRingRadiusLimit)
      {
        p.color = this.colors[0+counter];
      }
      else if(distance < this.outerRingRadiusLimit && distance > this.innerRingRadiusLimit)
      {
        p.color = this.colors[3+counter];
      }
      else
      {
        p.color = this.colors[6+counter];
      }
      p.debug && console.log()
      this.c.beginPath();
      this.c.arc(p.x, p.y, p.radius, 0, Math.PI *2, false);
      this.c.fillStyle = p.color;
      this.c.fill();
      this.c.closePath();
      counter = counter == 2 ? 0 : counter+1;
    }
  }

  animate()
  {
    if(this.stopAnimation){ return; }
    requestAnimationFrame(this.animate);
    this.c.fillStyle = 'rgba(0, 0, 0, 0.05)';
    this.c.fillRect(0, 0, this.width, this.height);
    this.updateParticles();
  }
}



class BlackHoleReact extends React.Component {
  componentWillUnmount()
  {
    this.animation.stopAnimation = true;
  }

  componentDidMount()
  {
    this.animation = new BlackHole('black-hole', this.props.width, this.props.height, this.props.centreText);
  }

  render() {
    return (
      <canvas id="black-hole"></canvas>
    )
  }  
}

export default BlackHoleReact;