import React from "react";
import BlackHoleReact from "../components/blackHole";

class PageNotFound extends React.Component {

  constructor(props)
  {
    super(props);
  }

  componentWillUnmount()
  {
    this.props.newAppClose();
  }

  componentDidMount()
  {
    this.props.newAppOpen(false);
  }

  render() {
    return (
      <main className="page-not-found">
        <BlackHoleReact width={window.innerWidth} height={window.innerHeight} centreText="<h1>404</h1><h4>Page not found!</h4>" />
      </main>
    )
  }
}

export default PageNotFound;