import React from "react";
import ReactDOM from "react-dom";
import {Route, Switch, NavLink } from "react-router-dom";
import {CSSTransition, TransitionGroup} from "react-transition-group";
import routes from '../data/routes';
import helpers from '../helpers/helperFunctions';

class Navigation extends React.Component {
  constructor(props)
  {
    super(props);
    this.openMenu = this.openMenu.bind(this);
    this.shutMenu = this.shutMenu.bind(this);
    this.state = {
      menuOpen: false,
      showMenuButton: false,
      menuButtonPosition: {}
    }
  }

  componentDidMount()
  {
    let initialDirection = "bottomRight";
    for(let route of routes)
    {
      if(window.location.pathname != "/" && route.path == window.location.pathname)
      {
        initialDirection = route.position;
      }
    }    
    if(window.location.pathname != "/")
    {
      this.setState({
        showMenuButton: true,
        menuButtonPosition: initialDirection == "topRight" ? {'left':'0px'} : {'right':'0px'}
      });
    }
  }

  componentDidUpdate(prevProps)
  {
    if (this.props.isMainObjectCentered !== prevProps.isMainObjectCentered) {
      this.openMenu();
    }
  }

  openMenu()
  {
    setTimeout(() => {
      this.setState({
        menuOpen: true
      });   
    }, 1000); 
    this.setState({
      showMenuButton: false
    });
  }

  shutMenu(position)
  {
    this.setState({
      menuOpen: false,
      showMenuButton: true
    });
    this.setState({
      menuButtonPosition: position == "topRight" ? {'left':'0px'} : {'right':'0px'}
    });
  }

  render() {
    return (
      <div className="navigation-wrapper">
        <NavLink activeClassName="active" exact to="/" className={"navigation-button " + (this.state.showMenuButton ? 'navigation-button-open' : '')} style={this.state.menuButtonPosition} onClick={()=>{this.props.navigationOccured('center'); this.openMenu();}}><span></span><span></span><span></span></NavLink>
        <nav className={"navigation " + (this.state.menuOpen ? 'navigation-open' : '')}>
          <ul className="no-list-items mb-0">
            {routes.map((route)=>(
              !route.dontDisplay && 
              <li key={route.name} className={route.position}>
                <NavLink activeClassName="active" exact to={route.path} onClick={()=>{this.props.navigationOccured(route.position); this.shutMenu(route.position);}}>{ route.name }</NavLink>
              </li>
            ))}
          </ul>   
        </nav>
        
        <div className="transition-wrapper">
          <Route render={({location})=>(
            <TransitionGroup>
              <CSSTransition
                key={location.key}
                timeout={1500}
                classNames="page-transition">
                <Switch location={location}>
                    {routes.map((route)=>(
                      <Route path={ route.path } exact key={route.path} render={(props) => <route.page {...props} position={route.position} {...this.props} />} />
                    ))}                
                </Switch>
              </CSSTransition>
            </TransitionGroup>          
          )} />
        </div>
      </div>
    );
  }
}

export default Navigation;