import React from "react";
import Skill from '../components/skill';
import skills from '../data/skills';

class SkillsPage extends React.Component {
  constructor(props)
  {
    super(props);
  }

  render() {
    return (
      <main className={"skills-page page " + this.props.position}>
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h4 className="t-center">Figures are based on confidence levels, experience and self rating in those areas. Nothing is ever one hundred percent as there is always something to learn.</h4>
              <i className="fas fa-angle-double-down t-center scroll-icon"></i>
              <div className="u-flex wrap space-even">
                {skills.map((skill)=>(
                  <Skill key={skill.skill} {...skill} />
                ))}
              </div>
            </div>
          </div>
        </div>
      </main>
    )
  }
}

export default SkillsPage;