import React from "react";
import { animatedCarouselSlides } from "../data/dummyData";

class AnimatedCarousel {
  constructor(selector)
  {
    this.ac = document.querySelector(selector);
    if(!this.ac){return;}
    this.background = this.ac.querySelector('.background');
    this.slidesContainer = this.ac.querySelector('.slide-container');
    this.slides = [...this.slidesContainer.querySelectorAll('.slide')];
    this.nextSlide = this.ac.querySelector('.next-slide');
    this.prevSlide = this.ac.querySelector('.prev-slide');
    this.width = this.ac.getBoundingClientRect().width;
    this.backgroundColors = [
      "#84fab0", 
      "#8fd3f4", 
      "#f78ca0", 
      "#fbc2eb", 
      "#fa709a", 
      "#a6c1ee", 
      "#f9748f", 
      "#d4fc79", 
      "#96e6a1", 
      "#84fab0", 
      "#fee140", 
      "#8fd3f4", 
      "#fd868c", 
      "#fe9a8b" 
    ];
    this.counter = 0;
    this.nextslide = this.nextslide.bind(this);
    this.prevslide = this.prevslide.bind(this);
    this.addEventListeners = this.addEventListeners.bind(this);
    this.addEventListeners();
    this.init();
  }

  addEventListeners()
  {
    this.nextSlide.addEventListener('click', this.nextslide);
    this.prevSlide.addEventListener('click', this.prevslide);    
    document.body.addEventListener('keyup', (ev) => {
      switch(ev.keyCode)
      {
        case 39:
        {
          this.nextSlide.click();
          break;
        }
        case 37:
        {
          this.prevSlide.click();
          break;
        }
      }
    });
  }

  init()
  {
    this.background.style.backgroundImage = `linear-gradient(to right,${this.backgroundColors.join(',')})`;
    this.background.style.backgroundSize = `${this.backgroundColors.length*this.width}px 100%`;
    this.background.style.backgroundPosition = "0 0";
    this.slidesContainer.style.width = `${this.slides.length*this.width}px`;
    this.slides.map((slide)=>{
      slide.style.width = `${this.width}px`;
      let content = slide.querySelector('.slide-content');
      let title = slide.querySelector('.slide-title');
      slide.addEventListener('mousemove', function(e){
        let rect = slide.getBoundingClientRect();
        let width = rect.width;
        let height = rect.height;
        let halfW = width/2;
        let halfH = height/2;
        let degY = e.x>=halfW ? Math.abs(halfW-e.x) : -(halfW-e.x);
        let degX = e.y>=halfH ? halfH-e.y : Math.abs(halfH-e.y);
        content.style.transform = `rotateY(${degY/100}deg) rotateX(${degX/30}deg) translate(${degY/40}px, ${degX/30}px)`;
        title.style.transform = `rotateY(${degY/100}deg) rotateX(${degX/30}deg) translate(${degY/40}px, ${degX/30}px)`;
      });
    });
  }

  nextslide()
  {
    if(this.counter<(this.slides.length-1))
    {
      this.counter++;
      this.slides[this.counter-1].style.transform = `translateX(${-(this.counter*this.width)}px) scale(0.2)`;
      this.slides[this.counter].style.transform = `translateX(${-(this.counter*this.width)}px) scale(1)`;
      this.background.style.backgroundPosition = `${-(this.counter*this.width)}px 0`;
    }
  }

  prevslide()
  {
    if(this.counter>0)
    {
      this.counter--;
      this.slides[this.counter].style.transform = `translateX(${-(this.counter*this.width)}px) scale(1)`;
      this.slides[this.counter+1].style.transform = `translateX(${-(this.counter*this.width)}px) scale(0.2)`;
      this.background.style.backgroundPosition = `${-(this.counter*this.width)}px 0`;
    }
  }
}

class AnimatedCarouselReact extends React.Component {
  componentDidMount()
  {
    new AnimatedCarousel('#animated-carousel');
  }
  render() {
    return (
      <div className="animated-carousel" id="animated-carousel">
        <div className="background"></div>
        <div className="prev-slide">&lt;</div>
        <div className="next-slide">&gt;</div>
        <div className="slide-container">
          {animatedCarouselSlides.map((dd, index)=>(          
            <div className="slide" key={"slide"+index}>
              <h1 className="slide-title">{dd.title}</h1>
              <div className="slide-content">
                <div dangerouslySetInnerHTML={dd.content}></div>
              </div>
            </div>
          ))}
        </div>    
      </div>  
    )
  }  
}
export default AnimatedCarouselReact;