import React from "react";
import Project from '../components/project';
import ProjectDetail from '../components/projectDetail';
import projects from '../data/projects';
import FlexTabs from '../components/flexTabs';
import helpers from '../helpers/helperFunctions';

class ProjectsPage extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
      projectDetail: null,
      tabNumber: 0
    }
    this.openProject = this.openProject.bind(this);
    this.closeProject = this.closeProject.bind(this);
    console
  }

  componentWillUnmount()
  {
    this.setState({
      projectDetail: null,
      tabNumber: 0
    })
  }

  componentDidMount()
  {
    let search = this.props.location.search.replace("?","");
    let isProject = projects.filter((project)=> project.name==search);
    isProject.length > 0 && this.openProject(isProject[0].pageData);
  }

  closeProject()
  {
    setTimeout(() => {
      this.setState({
        projectDetail:null
      });
    }, 1000);
    this.setState({
      tabNumber: 0
    })
  }

  openProject(projectDetail)
  {
    this.setState({
      projectDetail: projectDetail,
      tabNumber: 1
    },
    ()=>{
      setTimeout(() => {
        window.scrollTo({top:0, behavior:'smooth'});
      }, 1000);
    }
    );
  }

  render() {
    return (
      <main className={"projects-page page " + this.props.position}>
        <FlexTabs tabNumber={this.state.tabNumber}>
          <div className="flex-tab-container">
            <div className="project-grid">
            {projects.map((project, index)=>(
              <Project key={project.name} openProject={this.openProject} media={project.media} {...project} />
            ))}
            </div>
          </div>
          <div className="flex-tab-container">
            {this.state.projectDetail && <i className="go-back-icon fas fa-arrow-left" onClick={this.closeProject}></i>}
            {this.state.projectDetail && <ProjectDetail project={ this.state.projectDetail }/>}
          </div>
        </FlexTabs>
      </main>
    )
  }
}
export default ProjectsPage;