import HomePage from '../pages/home';
import SkillsPage from '../pages/skills';
import ProjectsPage from '../pages/projects';
import ContactPage from '../pages/contact';
import JustForFunPage from '../pages/just-for-fun';
import AboutPage from '../pages/about';
import PageNotFound from '../pages/404';

const routes = [
  {
    path: '/',
    page: HomePage,
    name: 'Home',
    dontDisplay: true
  },
  {
    path: '/skills/',
    page: SkillsPage,
    name: 'Skills',
    position: 'topLeft'
  },
  {
    path: '/projects/',
    page: ProjectsPage,
    name: 'Projects',
    position: 'bottomRight'
  },
  {
    path: '/contact/',
    page: ContactPage,
    name: 'Get in touch!',
    position: 'bottomLeft'
  },
  {
    path: '/just-for-fun/',
    page: JustForFunPage,
    name: 'Just For Fun',
    position: 'topRight'
  },
  /*
  {
    path: '/about/',
    page: AboutPage,
    name: 'About'
  },
  */
  {
    path: '*',
    page: PageNotFound,
    position: 'bottomRight',
    dontDisplay: true
  }
]

export default routes;