import React from "react";
import ReactDOM from "react-dom";

class FlexTabs extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
      numberOfTabs: props.children.length,
      tab: this.props.tabNumber || 0
    }
  }

  componentDidUpdate(prevProps)
  {
    if (this.props.tabNumber !== prevProps.tabNumber) {
      this.setState({
        tab: this.props.tabNumber
      })
    }
  }

  render()
  {
    return (
      <div className="flex-tab" style={{width:`${this.state.numberOfTabs*100}%`}}>
      {this.props.children.map((child, index)=> 
        <div key={"tab"+index} className="flex-tab-tab" style={{transform: `translateX(-${this.state.tab*100}%)`}}>{child}</div>
      )}
      </div>
    )
  }
}

export default FlexTabs;