import React from "react";
import ReactDOM from "react-dom";

class ProjectDetail extends React.Component {
  render(){
    return (
      <div className="project-details">
        {
          this.props.project.heading && 
          <h1 className="heading t-center">{ this.props.project.heading }</h1>
        }
        {
          this.props.project.text &&
          <section className="t-left" dangerouslySetInnerHTML={this.props.project.text}>
          </section>
        }
        {
          this.props.project.content && 
          this.props.project.content.map((content, i) => (
            <section key={`content-${i}`} className="u-flex align-center wrap">
              <div>
                {
                  content.media &&
                  content.media.includes('.mp4') ?
                  <video controls>
                    <source src={ content.media } type="video/mp4" />
                    Your browser does not support video
                  </video>
                  :
                  <img src={content.media} />
                }
              </div>
              <div dangerouslySetInnerHTML={content.text}>
              </div>
            </section>
          ))
        }
      </div>
    )
  }

}

export default ProjectDetail;