import React from "react";
import ReactDOM from "react-dom";
import helpers from "../helpers/helperFunctions";

class Project extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="project">
        <div className="perspective u-flex">
          <div className="transform u-flex">
            <div className="transform-face u-flex">
              <div onClick={ ()=>{this.props.openProject(this.props.pageData)} } className="card">
                { this.props.media ? <div className="card-image" style={{backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url(${this.props.media})`}}></div> : null}
                <div className="card-content">
                  <h4>{ this.props.displayName }</h4>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Project;