import React from "react";
import ReactDOM from "react-dom";
import welcomeMessage from '../data/welcomeMessage';
import helpers from '../helpers/helperFunctions';

let EasingFunctions = helpers.getEasingFunctions();
let parallaxMouse;

class UniverseStars extends React.Component {
  constructor(props)
  {
    super(props);
    this.messageRef = React.createRef();
    this.stars = [];
    this.welcomeMessageDesktopWidth = 700; //measured manually
    this.welcomeMessageMobileWidth = 460; //measured manually
    this.welcomeMessageDesktopHeight = 75; //measured manually
    this.welcomeMessageLineGapVertical = 50;
    this.welcomeMessageLineGapHorizontal= 265;
    this.welcomeMessageMobileHeight = (this.welcomeMessageDesktopHeight*2) + this.welcomeMessageLineGapVertical; //measured manually
    this.welcomeMessageDistanceFromTopDesktop = 0;
    this.welcomeMessageDistanceFromTopMobile = 0;
    this.gunSpacingFromTextDesktop = 75;
    this.gunSpacingFromTextMobile = 50;
    this.initAnimateComplete = 0;
    this.numberOfParticles = 0;
    this.animate = this.animate.bind(this);
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.stopAnimation = false;
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    parallaxMouse = {
      x: 0,
      y: 0
    }
  }

  componentWillUnmount()
  {
    this.stopAnimation = true;
  }

  componentDidMount() {
    this.isMobile = window.innerWidth < this.welcomeMessageDesktopWidth;
    this.canvas = this.messageRef.current;
    this.c = this.canvas.getContext('2d');
    this.pixelRatio = helpers.scaleCanvas(this.canvas, this.c, this.width, this.height);
    let gunY = this.isMobile ? this.welcomeMessageDistanceFromTopMobile : this.welcomeMessageDistanceFromTopDesktop;
    let gun2Y = this.isMobile ? this.welcomeMessageDistanceFromTopMobile + this.welcomeMessageMobileHeight + (this.gunSpacingFromTextMobile*2) : this.welcomeMessageDistanceFromTopDesktop + this.welcomeMessageDesktopHeight + (this.gunSpacingFromTextDesktop*2);
    this.gun = new Gun(this.c, this.width/2, gunY, 0, false);
    this.gun2 = new Gun(this.c, this.width/2, gun2Y, 0, true);
    this.createParticles();
    if(window.location.pathname != "/")
    {
      this.straightToZoomedOut = true;
      this.gun.destroyed = true;
      this.gun2.destroyed = true;
    }
    this.animate();
  }

  createParticles()
  {
    let x,
        y,
        mobileShiftY = 0,
        timeout = 0,
        mobileShiftX = 50,
        gun = this.gun,
        wp = [],
        explodeParams,
        centerx = this.width/2,
        centery = this.height/2,
        radius = Math.sqrt(Math.pow((this.width - this.width/2), 2) + Math.pow((this.height - this.height/2), 2)) + 100,
        debug,
        thingWidth,
        gunSpacing,
        shiftX,
        shiftY,
        wmp,
        zoomCoords = {};
    for(let [index, wm] of welcomeMessage.entries())
    {
      wmp=[];
      for(let [index2, wmc] of wm.coordinates.entries())
      {
        debug = (index==0 && index2==0);
        shiftX = this.isMobile ? mobileShiftX : 0;
        shiftY = this.isMobile ? mobileShiftY : 0;
        thingWidth = this.isMobile ? this.welcomeMessageMobileWidth : this.welcomeMessageDesktopWidth;
        gunSpacing = this.isMobile ? this.gunSpacingFromTextMobile : this.gunSpacingFromTextDesktop;
        y = (wmc.y / this.pixelRatio) + this.gun.y + gunSpacing + shiftY; 
        x = (wmc.x / this.pixelRatio) + ((this.width - thingWidth) / 2) + shiftX;
        explodeParams = {
          finalPositionX: helpers.randomInt(0, this.width),
          finalPositionY: helpers.randomInt(0, this.height),
          radius: helpers.randomInt(1,3)
        }
        explodeParams.bezierControlPoints = {
          x1: helpers.randomInt((explodeParams.finalPositionX-150), (explodeParams.finalPositionX+150)),
          y1: helpers.randomInt((explodeParams.finalPositionY-150), (explodeParams.finalPositionY+150)),
          x2: helpers.randomInt((explodeParams.finalPositionX-50), (explodeParams.finalPositionX+50)),
          y2: helpers.randomInt((explodeParams.finalPositionY-50), (explodeParams.finalPositionY+50))
        }  
        zoomCoords = helpers.closestPointOnCircumference(radius, {x:centerx, y:centery}, {x:explodeParams.finalPositionX, y:explodeParams.finalPositionY});
        let wp = new Star(this.c, x, gun.y, y, x, timeout, gun, explodeParams, zoomCoords, debug);
        this.numberOfParticles++;
        wmp.push(wp);
        timeout += 2;
      }
      wp.push(wmp);
      if(wm.letter == "!")
      {
        mobileShiftY = this.welcomeMessageLineGapVertical*2;
        mobileShiftX = -window.innerWidth/2/this.pixelRatio-50;
        gun = this.gun2;
        timeout = 0;
      }      
    }
    this.stars = wp;
  }

  zoom(direction)
  {
    this.explodeParticles = false;
    this.straightToZoomedOut = false;
    if(direction == "out")
    {
      this.removeMouseMove();
      this.zoomIn = false;
      this.zoomOut = true;
    }
    else if(direction == "in")
    {
      parallaxMouse.x = 0;
      parallaxMouse.y = 0;
      this.addMouseMove();
      this.zoomOut = false;
      this.zoomIn = true;
    }
  }

  fastForwardParticles()
  {
    this.gun.destroyed = true;
    this.gun2.destroyed = true;
    this.fastForward = true;
  }

  explodeStars()
  {
    this.fastForward = false;
    this.explodeParticles = true;
    this.addMouseMove();
  }

  removeMouseMove()
  {
    document.body.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('deviceorientation', this.handleOrientation);
  }

  addMouseMove()
  {
    document.body.addEventListener('mousemove', this.handleMouseMove);
    window.addEventListener('deviceorientation', this.handleOrientation);
  }

  handleOrientation(event)
  {
    let alpha = event.alpha === null ? 0 : event.alpha;
    let y = event.beta === null ? 0 : event.beta;
    let x = event.gamma === null ? 0 : event.gamma;
    if (x >  90) { x =  90};
    if (x < -90) { x = -90};
    parallaxMouse.x = (window.innerWidth*x/180)*4;
    parallaxMouse.y = (window.innerHeight*y/225)*4;  
  }

  handleMouseMove(e)
  {
    parallaxMouse.x = (e.clientX || e.pageX) - window.innerWidth/2;
    parallaxMouse.y = (e.clientY || e.pageY) - window.innerHeight/2; 
  }

  haltAnimations()
  {
    this.stopAnimation = true;
  }

  resumeAnimations()
  {
    this.stopAnimation = false;
    this.animate();
  }

  animate()
  {
    if(this.stopAnimation){ return; }
    requestAnimationFrame(this.animate);
    this.c.clearRect(0, 0, this.width, this.height);
    for(let wp of this.stars)
    {
      for(let wpp of wp)
      {
        if(this.explodeParticles)
        { 
          wpp.explode();
        }
        else if(this.zoomOut)
        {
          wpp.zoomOut();
          if(wpp.zoomedIn)
          {
            wpp.zoomedIn = false;
            wpp.zoomedInCache = false;
            wpp.resetLastMouse = false;
          }
        }
        else if (this.zoomIn)
        {
          wpp.zoomIn();
          if(wpp.zoomedOut)
          {
            wpp.zoomedOut = false;
            wpp.zoomOutCache = false;
          }
        }
        else if (this.straightToZoomedOut)
        {
          wpp.straightToZoomedOut();
        }
        else if (this.fastForward)
        {
          wpp.float = true;
          wpp.floatParticles();
        }
        else
        {
          wpp.fireParticles();
        }
        if(wpp.float && !wpp.dontPush)
        {
          this.initAnimateComplete++;
          wpp.dontPush = true;
        }        
      }
    }
    if(!this.gun.destroyed && !this.gun2.destroyed)
    {
      this.gun.update();
      this.gun2.update();
    }
    if(this.initAnimateComplete == this.numberOfParticles)
    {
      if(!this.addedEventListener)
      {
        this.props.welcomeMessageFinished();
      }
      this.addedEventListener = true;
    }
  }

  render() {
    return (
      <div className="universe-stars-container">
        <canvas ref={this.messageRef} />
      </div>
    )
  }
}

function Gun(c, x, y, moveToX, antiClockWise)
{
  this.gunSize = 20;
  this.gunDrag = 0.50;
  this.startX = x;
  this.dontFire = false;
  this.rotation = 0;
  this.opacity = parseFloat(1);
  this.fadeOut = parseFloat(0.05);
  Object.assign(this, {x, y, moveToX});

  this.update = function() {
    if(this.x == this.moveToX || this.dontFire == true)
    {
      this.startPos();
      this.dontFire = true;
    }
    else if(!this.dontFire)
    {
      this.fireParticles();
    }
    this.draw();
  }

  this.fireParticles = function() {
    this.x += (this.moveToX-this.x) * this.gunDrag;
  }

  this.startPos = function() {
    this.x += (this.startX-this.x) * (this.gunDrag/4);
    this.fade();
  }

  this.fade = function() {
    if(this.opacity <= 0)
    {

      this.destroyed = true;
    }
    else
    {
      this.opacity = (this.opacity - this.fadeOut).toFixed(2);
    }
  }

  this.draw = function() {
    c.save();
    c.globalAlpha = this.opacity;
    c.beginPath();
    c.fillStyle = "#f47740";
    c.arc(this.x, this.y, this.gunSize, 0, Math.PI, antiClockWise);
    c.closePath();
    c.fill();
    c.restore();
  }
}

function Star(c, x, y, moveToY, moveToX, timeout, gun, explodeParams, zoomCoords, debug)
{
  Object.assign(this, {x, y, moveToY, moveToX, timeout, gun, explodeParams, zoomCoords});
  this.starRadius = 1;
  this.starFromGunSpeed = 0.20;
  this.starFloatSpeed = 0.065;
  this.floatingDistance = 1;
  this.radians = 0;
  this.index = 0;
  this.float = false;
  this.explodeDrag = 0.005;
  this.p0 = {x:moveToX, y:moveToY};
  this.p1 = {x:explodeParams.bezierControlPoints.x1, y:explodeParams.bezierControlPoints.y1};
  this.p2 = {x:explodeParams.bezierControlPoints.x2, y:explodeParams.bezierControlPoints.y2};
  this.p3 = {x:explodeParams.finalPositionX, y:explodeParams.finalPositionY};
  this.t = 0;
  this.parallaxMoveValue = 2;
  this.parallaxMoveDrag = Math.random() /  15;
  this.parallaxMoveAmount = Math.floor(Math.random() * (20- this.parallaxMoveValue+1) +  this.parallaxMoveValue);
  this.lastMouse = {x:0,y:0};
  this.zoomDuration = 2000;

  this.easingFunction = function(timingfn, flag, duration, variable='start')
  {
    if(!this[variable])
    {
      this[variable] = Date.now();
    }
    let p = (Date.now() - this[variable]) / duration;
    let val  = EasingFunctions[timingfn](p);
    if (Date.now() - this[variable] > duration){
      delete this[variable];
      if(flag){this[flag] = true;}
      return 1;
    }
    else
    {
      return val.toFixed(8);      
    }
  }

  this.explode = function()
  {
    this.cx = 3 * (this.p1.x - this.p0.x)
    this.bx = 3 * (this.p2.x - this.p1.x) - this.cx;
    this.ax = this.p3.x - this.p0.x - this.cx - this.bx;
    this.cy = 3 * (this.p1.y - this.p0.y);
    this.by = 3 * (this.p2.y - this.p1.y) - this.cy;
    this.ay = this.p3.y - this.p0.y - this.cy - this.by;
    if(this.starRadius <= this.explodeParams.radius)
    {
      this.starRadius += 0.1;
    }
    if(this.t>=1)
    {
      this.t=1;
      this.parallax();
    }
    else
    {
      this.t += this.explodeDrag;
      this.x = this.ax*(this.t*this.t*this.t) + this.bx*(this.t*this.t) + this.cx*this.t + this.p0.x;
      this.y = this.ay*(this.t*this.t*this.t) + this.by*(this.t*this.t) + this.cy*this.t + this.p0.y;      
    }
    this.draw();
  }

  this.straightToZoomedOut = function()
  {
    this.x = this.zoomCoords.x;
    this.y = this.zoomCoords.y;
  }

  this.zoomOut = function()
  {
    if(!this.zoomedOut)
    {
      let val = this.easingFunction('easeOutQuad', 'zoomedOut', this.zoomDuration);
      if(!this.zoomOutCache)
      {
        this.currentParticlePosition = {
          x: this.x,
          y: this.y
        };
      }
      this.zoomOutCache = true;
      this.x = this.currentParticlePosition.x + (this.zoomCoords.x - this.currentParticlePosition.x) * val;
      this.y = this.currentParticlePosition.y + (this.zoomCoords.y - this.currentParticlePosition.y) * val;
    }
    this.draw();
  }

  this.resetParallax = function()
  {
    if(!this.resetLastMouse)
    {
      this.lastMouse.x = 0;
      this.lastMouse.y = 0;
      this.resetLastMouse = true;
    }
  }

  this.zoomIn = function()
  {
    if(!this.zoomedIn)
    {
      let val = this.easingFunction('easeInCubic', 'zoomedIn', this.zoomDuration, 'start2');
      if(!this.zoomInCache)
      {
        this.currentParticlePosition = {
          x: this.x,
          y: this.y
        };
      }
      this.x = this.currentParticlePosition.x - (this.currentParticlePosition.x - this.explodeParams.finalPositionX) * val;
      this.y = this.currentParticlePosition.y - (this.currentParticlePosition.y - this.explodeParams.finalPositionY) * val;
      this.zoomInCache = false;
    }
    else
    {
      this.resetParallax();
      this.parallax();
    }
    this.draw();    
  }

  this.parallax = function()
  {
    this.x = this.explodeParams.finalPositionX + (this.lastMouse.x/this.parallaxMoveAmount);
    this.y = this.explodeParams.finalPositionY + (this.lastMouse.y/this.parallaxMoveAmount);
    this.lastMouse.x += (parallaxMouse.x - this.lastMouse.x) * this.parallaxMoveDrag;
    this.lastMouse.y += (parallaxMouse.y - this.lastMouse.y) * this.parallaxMoveDrag;      
  }

  this.fireParticles = function() {
    if(this.timeout == 0)
    {
      if(this.float == true)
      {
        this.floatParticles();
      }
      else
      {
        this.init();
      }
    }
    else
    {
      this.timeout -= 1;
    }
  }

  this.init = function() {
    this.y += (this.moveToY-this.y) * this.starFromGunSpeed;
    if(this.hasCompleted(this.y, this.moveToY))
    {
      this.float = true;
    }
    this.gun.moveToX = this.moveToX;
    this.draw();
  }

  this.hasCompleted = function(coord, compare)
  {
    return (Math.floor(coord) == Math.floor(compare) || Math.ceil(coord) == Math.ceil(compare));
  }

  this.floatParticles = function() {
    this.radians += this.starFloatSpeed;
    this.y = this.moveToY + Math.cos(this.radians) * this.floatingDistance; 
    this.draw();
  }

  this.draw = function() {
    c.beginPath();
    c.arc(this.x, this.y, this.starRadius, 0, Math.PI *2, false);
    c.fillStyle = "white";
    c.fill();
    c.closePath();
  }
}
export default UniverseStars;